body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-6xl: 25px;
  --font-size-21xl: 40px;
  --font-size-base: 16px;
  --font-size-16xl: 35px;

  /* Colors */
  --color-black: #000;
  --color-silver: #ccc8c8;
  --color-white: #fff;

  /* border radiuses */
  --br-mini: 15px;
}
